:root {
  --border-colour: #43498a;
  --contact-banner-bg-colour: #add19e;
  --contact-banner-font-colour: black;
  --home-banner-font-colour: white;
  --home-mission-bg-colour: #F0F1F8;
  --nav-link-text-colour: white;
  --nav-link-text-colour-selected: #43498a;
  --nav-link-bg-colour-selected: white;
  --nav-bg-colour: #43498a;
  --mobile-nav-btn-bg-colour: #add19e;
  --mobile-nav-btn-border-colour: #F0F1F8;
  --mobile-nav-btn-icon-colour: #43498a;
  --mobile-nav-menu-bg-colour-start: #43498a;
  --mobile-nav-menu-bg-colour-end: #add19e;
  --quotes-bg-colour: #add19e;
  --quotes-box-bg-colour: #a7ce97;
  --policies-banner-bg-colour: #add19e;
  --policies-banner-font-colour: black;
  --policies-box-bg-colour: #F0F1F8;
  --about-subtext-colour: #5d5f57;
  --contact-box-bg-colour: #F0F1F8;
  --credentials-tab-text-colour: black;
  --credentials-tab-text-colour-selected: black;
  --credentials-tab-bg-colour-selected: #F0F1F8;
  --credentials-tab-bg-colour: #add19e;
  --link-colour: #5e60ce;
  --map-circle-outline-colour: #43498a;
  --map-circle-fill-colour: #5e60ce;
  --menu-height: max(15vh, 60px);
  --mobile-menu-icon-height: calc(var(--menu-height) * 0.5);
}
.centre,
nav > .desktop-menu > .logo,
nav > .desktop-menu > .nav-item,
nav > .mobile-menu section > div > a,
.container > .mission,
.container > .info-panels > .info-panel {
  display: flex;
  justify-content: center;
  align-items: center;
}
.middle,
nav > .desktop-menu > .logo {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.left {
  display: flex;
  justify-content: left;
  align-items: flex-start;
}
.right {
  display: flex;
  justify-content: right;
  align-items: flex-end;
}
.text-left {
  text-align: left;
}
.text-centre,
.container > .mission,
.container > section.quotes h1,
.container > section.quotes blockquote.quote > p,
.container > .info-panels > .info-panel {
  text-align: center;
}
.text-right {
  text-align: right;
}
* {
  box-sizing: border-box;
}
html,
body {
  margin: 0;
  padding: 0;
  height: 100vh !important;
  width: 100vw !important;
  overflow-x: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
html::-webkit-scrollbar,
body::-webkit-scrollbar {
  display: none;
}
a {
  color: var(--link-colour);
}
body {
  font-family: 'Noto Sans', sans-serif;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: var(--menu-height) 1fr min-content;
  grid-auto-columns: 1fr;
  grid-auto-rows: 1fr;
  gap: 0px 0px;
  grid-auto-flow: row;
  grid-template-areas: 'nav' 'container' 'footer';
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
h1:not(.rootfont),
h2:not(.rootfont),
h3:not(.rootfont),
h4:not(.rootfont),
h5:not(.rootfont),
h6:not(.rootfont) {
  font-family: 'Josefin Sans', sans-serif;
}
.vertical-pad-1rem {
  padding-top: 1rem;
  padding-bottom: 1rem;
}
.horizontal-pad-1rem {
  padding-left: 1rem;
  padding-right: 1rem;
}
.vertical-pad-1 {
  padding-top: 1em;
  padding-bottom: 1em;
}
.horizontal-pad-1,
.container > section.banner {
  padding-left: 1em;
  padding-right: 1em;
}
nav {
  grid-area: nav;
  width: 100vw;
  height: var(--menu-height);
}
nav > .desktop-menu {
  color: var(--home-banner-font-colour);
  background-color: var(--nav-bg-colour);
  padding-left: 2em;
  padding-right: 2em;
  height: 100%;
  display: grid;
  grid-template-columns: 25vmin 1fr min-content min-content min-content min-content min-content;
  grid-template-rows: var(--menu-height);
  grid-auto-columns: 1fr;
  gap: 0.5em 0.5em;
  grid-auto-flow: row;
  grid-template-areas: "logo spacing home about credentials policies contact";
}
nav > .desktop-menu > .logo {
  grid-area: logo;
  margin: 0.5em;
}
nav > .desktop-menu > .home {
  grid-area: home;
}
nav > .desktop-menu > .about {
  grid-area: about;
}
nav > .desktop-menu > .credentials {
  grid-area: credentials;
}
nav > .desktop-menu > .policies {
  grid-area: policies;
}
nav > .desktop-menu > .contact {
  grid-area: contact;
}
nav > .desktop-menu > .nav-item > a {
  text-decoration: none;
  color: var(--nav-link-text-colour);
  font-size: larger;
  font-weight: bold;
  padding: 2em;
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  margin: 5px;
  border-radius: 10px;
}
nav > .desktop-menu > .nav-item.selected > a {
  color: var(--nav-link-text-colour-selected);
  background-color: var(--nav-link-bg-colour-selected);
  opacity: 1;
  transition: opacity 0.3s;
}
nav > .desktop-menu > .nav-item:not(.selected) > a:hover {
  background-color: var(--nav-link-bg-colour-selected);
  color: var(--nav-link-text-colour-selected);
  opacity: 0.7;
}
nav > .mobile-menu {
  display: none;
  z-index: 1;
  user-select: none;
  height: 0;
}
nav > .mobile-menu > a {
  text-decoration: none;
}
nav > .mobile-menu > label,
nav > .mobile-menu > a {
  position: relative;
  left: calc(100vw - var(--mobile-menu-icon-height) - (var(--mobile-menu-icon-height) / 3.33333333));
  bottom: calc(var(--menu-height) * 0.8);
  z-index: 4;
}
nav > .mobile-menu > label > span,
nav > .mobile-menu > a > span.material-icons {
  font-size: var(--mobile-menu-icon-height);
  background-color: var(--mobile-nav-btn-bg-colour);
  border-radius: 3px;
  color: var(--mobile-nav-btn-icon-colour);
  text-shadow: 0px 0px 5px var(--mobile-nav-btn-icon-colour), 0px 0px 2px black;
  box-shadow: 0px 0px 10px -2px #000000;
  z-index: 5;
  transition: all 1s ease-in-out;
}
nav > .mobile-menu section {
  display: grid;
  position: absolute;
  top: 0;
  left: -100vw;
  opacity: 0%;
  grid-auto-columns: 1fr;
  grid-auto-rows: 1fr;
  gap: 0px 0px;
  justify-content: space-around;
  align-content: space-evenly;
  justify-items: start;
  align-items: center;
  transition: all 0.8s ease-in-out;
  background-color: var(--mobile-nav-menu-bg-colour-start);
  background: radial-gradient(circle at 93.5% 5.7%, var(--mobile-nav-menu-bg-colour-end) 0%, var(--mobile-nav-menu-bg-colour-end) 5%, var(--mobile-nav-menu-bg-colour-start) 95%, var(--mobile-nav-menu-bg-colour-start) 100%);
  width: 100vw;
  height: 100vh;
  z-index: -1;
}
nav > .mobile-menu section > div {
  width: 50%;
  margin: auto;
}
nav > .mobile-menu section > div > a {
  text-decoration: none;
  color: var(--nav-link-text-colour);
  font-size: larger;
  font-weight: bold;
  padding: 2em;
  margin: 5px;
  border-radius: 10px;
}
nav > .mobile-menu section > div > a.selected {
  color: var(--nav-link-text-colour-selected);
  background-color: var(--nav-link-bg-colour-selected);
  opacity: 1;
}
nav > .mobile-menu > input {
  display: none;
}
nav > .mobile-menu > input:checked ~ section {
  opacity: 100%;
  left: 0;
  z-index: 3;
}
nav > .mobile-menu > input:checked ~ label > span {
  box-shadow: none;
  background-color: transparent;
}
.container {
  grid-area: container;
  width: 100%;
}
.container > main {
  padding: 5em;
  padding-top: 1em;
  padding-bottom: 0 !important;
}
.container > section.banner {
  background-color: var(--home-banner-bg-colour);
  color: var(--home-banner-font-colour);
  text-align: center;
  font-size: xx-large;
  padding: 20vh;
  max-height: min-content;
  width: 100%;
}
footer {
  grid-area: footer;
  width: fit-content;
  height: 5vh;
  margin: auto;
}
footer > span {
  display: inline-block;
  position: relative;
  top: 1.5ex;
}
@media screen and (max-width: 1240px) {
  nav > .desktop-menu > .nav-item > a {
    font-size: medium;
  }
}
@media screen and (max-width: 1080px) {
  nav > .desktop-menu > .nav-item > a {
    padding: 1.5em;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
  }
}
@media screen and (max-width: 940px) {
  :root {
    --menu-height: max(12vh, 60px);
  }
  nav > .desktop-menu {
    grid-template-columns: 1fr min-content min-content min-content min-content min-content;
    grid-template-areas: "logo home about credentials policies contact";
  }
  nav > .desktop-menu > .logo {
    margin-top: 1em;
    margin-bottom: 1em;
  }
  nav > .desktop-menu > .logo > img {
    width: unset;
    height: 100%;
    align-self: flex-start;
  }
}
@media screen and (max-width: 880px) {
  nav > .desktop-menu > .logo {
    margin-top: 1em;
    margin-bottom: 1em;
  }
  nav > .desktop-menu > .logo > img {
    width: unset;
    height: 100%;
    align-self: flex-start;
  }
  nav > .desktop-menu > .nav-item > a {
    padding: 1em;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
  }
}
@media screen and (max-width: 750px) {
  .container > section.banner {
    padding-left: 3em;
    padding-right: 3em;
  }
}
@media screen and (max-width: 780px) {
  nav > .desktop-menu {
    gap: 0;
    padding-right: 0.5em;
    padding-left: 0.5em;
  }
}
@media screen and (max-width: 715px) {
  nav > .desktop-menu > .nav-item {
    display: none;
  }
  nav > .mobile-menu {
    display: block;
    transition-duration: 1s;
  }
}
@media screen and (max-width: 400px) {
  .container > section.banner {
    font-size: x-large;
  }
  .container > section.mission {
    font-size: 1em;
  }
}
@media screen and (max-width: 460px) {
  .container > section.banner {
    padding-left: 1em;
    padding-right: 1em;
  }
}
@media screen and (max-width: 395px) {
  footer {
    margin-top: 1em;
    margin-bottom: 1em;
  }
  footer > span {
    display: block;
    text-align: center;
    top: 0;
  }
}
@media screen and (max-width: 375px) {
  nav > .mobile-menu > section {
    padding-top: var(--mobile-menu-icon-height);
  }
  nav > .mobile-menu > section > div {
    width: 75%;
  }
}
footer {
  margin-top: 2em;
}
.container > section.banner {
  background-image: linear-gradient(rgba(0, 0, 128, 0.25), rgba(0, 0, 128, 0.25)), url(/assets/img/home-bg.jpg);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: inset 0px 0px 15px -2px #000000;
  transform: translateY(-1px);
  min-height: 60vh;
}
.container > .mission {
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  grid-template-rows: minmax(20vh, min-content);
  gap: 0px 2em;
  grid-auto-flow: row;
  grid-template-areas: "line1 text line2";
  box-shadow: 0px 0px 5px -2px #000000;
  background-color: var(--home-mission-bg-colour);
  border-radius: 3px;
  margin: 1em;
  font-size: 1.25em;
}
.container > .mission h1 {
  grid-area: text;
}
.container > .mission .line1 {
  border-radius: 3px;
  border: 2px solid var(--border-colour);
  border-style: dashed;
  border-top: none;
  border-left: none;
  border-right: none;
  border-radius: 0;
  height: 5%;
  grid-area: line1;
}
.container > .mission .line2 {
  border-radius: 3px;
  border: 2px solid var(--border-colour);
  border-style: dashed;
  border-top: none;
  border-left: none;
  border-right: none;
  border-radius: 0;
  height: 5%;
  grid-area: line2;
}
.container > section.quotes {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: min-content repeat(2, min-content);
  gap: 1.5em;
  grid-auto-flow: row;
  grid-template-areas: "title title title title" "quote1 quote1 quote2 quote2" ". quote3 quote3 .";
  border-top: 2px solid var(--border-colour);
  border-bottom: 2px solid var(--border-colour);
  background-color: var(--quotes-bg-colour);
  padding: 1em;
}
.container > section.quotes h1 {
  grid-area: title;
  font-family: 'Raleway', sans-serif;
  font-weight: 600;
  font-size: xx-large;
}
.container > section.quotes blockquote.quote {
  border-radius: 3px;
  padding: 2em 1em;
  background-color: var(--quotes-box-bg-colour);
  position: relative;
  box-shadow: inset 0px 0px 5px -2px #000000;
}
.container > section.quotes blockquote.quote::before {
  font-family: 'Gulzar', 'Georgia', serif;
  content: "“";
  font-size: 8vw;
  position: absolute;
  top: -1.2ex;
  left: 20px;
}
.container > section.quotes blockquote.quote > p {
  font-size: 1.5vw;
}
.container > section.quotes blockquote.quote.quote-1 {
  grid-area: quote1;
}
.container > section.quotes blockquote.quote.quote-2 {
  grid-area: quote2;
}
.container > section.quotes blockquote.quote.quote-3 {
  grid-area: quote3;
}
.container > .info-panels {
  display: grid;
  grid-template-columns: 25vw 1fr 25vw;
  grid-template-rows: min-content 1fr;
  gap: 0 1em;
  grid-auto-flow: row;
  grid-template-areas: "title title title" "panel1 panel2 panel3";
}
.container > .info-panels > .info-panel {
  flex-direction: column;
  padding: 1em;
  width: 100%;
  height: 100%;
}
.container > .info-panels > .info-panel > h1 {
  font-family: 'Raleway', sans-serif;
  font-weight: 600;
  font-size: xx-large;
}
.container > .info-panels > .info-panel > p {
  width: 75%;
  font-size: large;
}
.container > .info-panels > .info-panel.panel-1 {
  grid-area: panel1;
}
.container > .info-panels > .info-panel.panel-2 {
  grid-area: panel2;
}
.container > .info-panels > .info-panel.panel-3 {
  grid-area: panel3;
}
.container > .info-panels > .info-panel.panel-title {
  grid-area: title;
}
.container > .info-panels > .info-panel > img {
  border-radius: 3px;
  border: 2px solid var(--border-colour);
  width: 100%;
  box-shadow: 0px 0px 5px -2px #000000;
}
@media screen and (max-width: 1100px) {
  .container > section.quotes > blockquote.quote > p {
    font-size: 2vw;
  }
}
@media screen and (max-width: 900px) {
  .container > section.quotes {
    gap: 0.5em 0.5em;
  }
}
@media screen and (max-width: 825px) {
  .container > section.quotes > blockquote.quote::before {
    font-size: 10vw;
  }
  .container > section.quotes > blockquote.quote > p {
    font-size: 2.5vw;
  }
}
@media screen and (max-width: 710px) {
  .container > .info-panels {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: repeat(3, min-content);
    gap: 1em 0;
    grid-template-areas: "title title" "panel2 panel2" "panel1 panel3";
  }
  .container > .info-panels > .info-panel.panel-2,
  .container > .info-panels > .info-panel.panel-2 > p {
    margin-top: 0;
  }
  .container > .info-panels > .info-panel.panel-1 {
    padding: 0.25em;
    padding-left: 0.5em;
  }
  .container > .info-panels > .info-panel.panel-3 {
    padding: 0.25em;
    padding-right: 0.5em;
  }
  .container > section.quotes {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, min-content);
    grid-template-areas: "title" "quote1" "quote2" "quote3";
    padding-left: 1.2em;
    padding-right: 1.2em;
  }
  .container > section.quotes > blockquote.quote {
    margin-left: 0;
    margin-right: 0;
  }
  .container > section.quotes > blockquote.quote::before {
    font-size: 16vw;
  }
  .container > section.quotes > blockquote.quote > p {
    font-size: 3.5vw;
  }
}
@media screen and (max-width: 540px) {
  .container > section.quotes > h1 {
    font-size: x-large;
  }
  .container > section.quotes > blockquote.quote > p {
    font-size: 4vw;
  }
}
@media screen and (max-width: 400px) {
  .container > section.quotes > blockquote.quote > p {
    font-size: 5vw;
  }
}
